* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}
a {
  color: #333;
  text-decoration: none;
}
.container {
  margin: 0 1rem;
}
.btn {
  border: none;
  outline: none;
  background: #555;
  color: #fff;
  font-size: 1em;
  cursor: pointer;
}
.btn:hover {
  background: #666;
}
